.item-identity-container {
    width: 173px;
    height: 209px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    opacity: 0.8;
    transition: 0.4s;
    cursor: pointer;
    border-radius: 4px 4px 0px 0px;
    position: relative;
    animation: slideAnimationItem 1s ease;
    z-index: 5;
}
@keyframes slideAnimationItem {
    0% {
      transform: translateX(-20px);
        opacity:0;
    }
    100% {
      transform: translateX(0);
      opacity:0.8;
    }
  }
.item-identity-container .shadow img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    position: relative; 
    border-radius: 8px 8px;
}
.item-identity-container .shadow{
    width: 100%;
    height: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px 8px;
}
.item-identity-container:hover {
    opacity: 1;
    transform: scale(1.02);
}

.item-identity-rarity {
    position: absolute;
    z-index: 102;
    width: fit-content;
    height: fit-content;
    justify-content: center;
    align-items: center;
    display: flex;
    box-sizing: border-box;
    font-size: 13.644px;
    font-style: normal;
    font-weight: 500;
    border-radius: 4px;
    background: rgba(0, 0, 0, 0.40);
    padding: 2px 6.4px 2px;
    margin: 4.8px 0px 0px 4.8px;
    font-size: 20px;
}

.item-identity-frame {
    width: 100%;
    height: 3px;
    position: absolute;
    bottom: 0;
    z-index: 102;
}

.item-identity-frame--OOO {
    background: var(--OOO-frame-color);
}

.item-identity-frame--OO {
    background: var(--OO-frame-color);
}

.item-identity-frame--O {
    background: var(--O-frame-color);
}

.item-identity-rarity--OOO {
    color: var(--OOO-color);
}

.item-identity-rarity--OO {
    color: var(--OO-color);
}

.item-identity-rarity--O {
    color: var(--O-color);
}

.item-identity-image {
    width: 120px;
    height: 120px;
}

.item-identity-name {
    color: #FFF;
    font-size: 20px;
    margin-bottom: 9.6px;
    box-sizing: border-box;
    text-align: left;
    padding: 0px 12px 0px 12px;
    position: absolute;
    bottom: 0;
    z-index: 102;
}
